form {
    max-width: 500px;
    margin: 0 auto;
}

.form {
    background: #0e101c;
    max-width: 400px;
    margin: 0 auto;
}
input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    border-radius: 4px;
    border: 1px solid white;
    padding: 10px 15px;
    margin-bottom: 10px;
    font-size: 14px;
}

label {
    line-height: 2;
    text-align: left;
    display: block;
    margin-bottom: 13px;
    margin-top: 20px;
    color: #ec5990;
    font-size: 14px;
    font-weight: 200;
}

button[type="submit"],
input[type="submit"] {
    background: #ec5990;
    color: white;
    text-transform: uppercase;
    border: none;
    margin-top: 40px;
    padding: 20px;
    font-size: 16px;
    font-weight: 100;
    letter-spacing: 10px;
}

button[type="submit"]:hover,
input[type="submit"]:hover {
    background: #bf1650;
}

button[type="submit"]:active,
input[type="button"]:active,
input[type="submit"]:active {
    transition: 0.3s all;
    transform: translateY(3px);
    border: 1px solid transparent;
    opacity: 0.8;
}

input:disabled {
    opacity: 0.4;
}

input[type="submit"] {
    -webkit-appearance: none;
}
.container_password {
    display: flex;
    flex-direction: row;
    /*justify-content: center;*/
    align-items: center;
    gap: 20px;
}
input[type="checkbox"] {
    width: 20px;
    height: 20px;
    margin-bottom: 0;
}